/* Start::app-header */
.app-header {
  position: fixed;
  left: 0px;
  right: 0px;
  top: 0px;
  z-index: 48;
  display: flex;
  height: 71px;
  width: 100%;
  flex-wrap: wrap;
  border-bottom: 1px solid var(--header-border-color);
  background: var(--header-bg);
  padding-top: 1rem;
  padding-bottom: 1rem;
  font-size: 0.875rem;
  line-height: 1.25rem;
  transition-property: all;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
