/* Start:: Products */
.product-icons {
  display: none;
}
.product-image img {
  background-color: $light;
}
.product-card {
  .mask-container {
    position: relative;
    overflow: hidden;

    &::before {
      content: "";
      position: absolute;
      height: 50px;
      width: 50px;
      border-end-end-radius: 250px;
      background-color: rgba(0,0,0, 0.5);
      inset-block-start: -45px;
      inset-inline-start: -45px;
      transform: scale(1);
      transition: all ease-in-out 250ms;
    }
  }

  .product-options {
    opacity: 0;
    transform: translate(-50%, 30%);
    transition: all ease 150ms;
    transition-delay: 80ms;
  }

  .product-badge {
    opacity: 1;
    transition: all ease-in-out 100ms;
  }

  &:hover {
    .mask-container::before {
      transform: scale(40);
    }

    .product-options {
      opacity: 1;
      transform: translate(-50%, -50%);
    }

    .product-badge {
      opacity: 0;
    }
  }
}
.a-center {
   inset-block-start: 50%;
   inset-inline-start: 50%;
   transform: translate(-50%, -50%);
}
.product-badge{
  inset-inline-end: 15px;
  inset-block-start: 15px;
}
.colorinput {
  margin: 0;
  position: relative;
  cursor: pointer;
}

.colorinput-input {
  position: absolute;
  z-index: -1;
  opacity: 0;
}

.colorinput-color {
  display: inline-block;
  width: 1.75rem;
  height: 1.75rem;
  border-radius: 50%;
  color: $white;
}

.colorinput-input:checked~.colorinput-color:before {
  opacity: 1;
}

.colorinput-color:before {
  content: "";
  opacity: 0;
  position: absolute;
  inset-block-start: 0.25rem;
  inset-inline-start: 0.25rem;
  height: 1.25rem;
  width: 1.25rem;
  transition: 0.3s opacity;
  background: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'%3E%3Cpath fill='%23fff' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26 2.974 7.25 8 2.193z'/%3E%3C/svg%3E") no-repeat center center/50% 50%;
}

/* End:: Products */

/* Start:: Product Details */
.swiper-view-details {
  .swiper-slide {
    border: 1px solid $default-border;
    border-radius: $default-radius !important;
    background-color: $light !important;
    &.swiper-slide-thumb-active {
      border: 1px solid $success !important;
    }
  }
}
.swiper-preview-details {
  .swiper-button-next {
    background-color: $black-1;
    // color: $custom-white !important;
  }
  .swiper-button-prev {
    background-color: $black-1;
  }
}
.product-colors {
  width: 2rem;
  height: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 3.125rem;
  border: 1px solid $default-border;
  margin-inline-end: 0.5rem;
  background-color: $light;
  i {
    font-size: 1.5rem;
  }
  &.color-2 {
    &.selected {
      border: 0.125rem solid $white;
    }
    i {
      color: $white;
    }
  }
  &.color-4 {
    &.selected {
      border: 0.125rem solid #514f4d;
    }
    i {
      color: #514f4d;
    }
  }
  &.color-1 {
    &.selected {
      border: 0.125rem solid #e9d2d1;
    }
    i {
      color: #e9d2d1;
    }
  }
  &.color-3 {
    &.selected {
      border: 0.125rem solid #c2c3c7;
    }
    i {
      color: #c2c3c7;
    }
  }
  &.color-5 {
    &.selected {
      border: 0.125rem solid #4a6181;
    }
    i {
      color: #4a6181;
    }
  }
}
.product-sizes {
  width: 2rem;
  height: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 3.125rem;
  border: 1px solid $default-border;
  margin-inline-end: 0.5rem;
  font-size: 0.75rem;
  background-color: $light;
  color: $text-muted;
  &.selected {
    background-color: rgba(var(--success-rgb), 0.1);
    color: $success;
  }
}
.offer-promotion {
  position: relative;
  padding: 0.5rem;
  background-color: rgba(var(--warning-rgb), 0.1);
  border: 1px solid $default-border;
  border-radius: $default-radius;
  .promotion-code {
    color: $default-text-color;
  }
  .offer-details {
    position: absolulte;
    border: 1px dashed $warning;
    padding: 0.25rem 0.5rem;
    border-radius: $default-radius;
  }
}
.ecommerce-assurance {
  border: 1px dashed $success;
  border-radius: $default-radius;
  text-align: center;
  padding: 1rem 1.5rem;
  svg {
    width: 3rem;
    height: 3rem;
  }
}
.product-images {
  padding-inline-start: 1.875rem;
  .products-review-images img {
    width: 3.125rem;
    height: 3.125rem;
    margin-inline-end: 0.25rem;
    border-radius: $default-radius;
    background-color: $light;
  }
}
.similar-products-image {
  img {
    width: 3.4rem;
    height: 3.4rem;
    padding: 5px;
    border-radius: $default-radius;
    border:1px solid $default-border;
  }
}
.similar-product-name {
  max-width: 80%;
}
.order-qnt{
 width: 125px;
}

.remove-choice{
  .choices__inner {
    min-width: 5rem !important;
  }

  .choices__list--dropdown .choices__item--selectable::after,
  .choices__list[aria-expanded] .choices__item--selectable::after {
    display: none !important;
  }

  .choices__list--dropdown .choices__item--selectable,
  .choices__list[aria-expanded] .choices__item--selectable {
    padding-inline-end: 0 !important;
  }
}

/* End:: Product Details */

/* Start:: Cart */
.product-quantity-container {
  width: 8.75rem;
  .input-group {
    input.form-control:focus {
      box-shadow: none;
    }
    .product-quantity-minus,
    .product-quantity-plus {
      &.btn {
        &:focus,
        &:hover {
          border-color: $input-border;
        }
      }
    }
  }
  .input-group
    > :not(:first-child):not(.dropdown-menu):not(.valid-tooltip):not(
      .valid-feedback
    ):not(.invalid-tooltip):not(.invalid-feedback) {
    margin-left: 0;
  }
}
.cart-empty svg {
  width: 6.25rem;
  height: 6.25rem;
  margin-block-end: 1.25rem;
  fill: $default-text-color;
}
/* End:: Cart */

/* Start:: Checkout */
.product-checkout {
  .tab-style-2 .nav-item .nav-link {
    padding: 0.85rem 2rem;
  }
  .form-floating {
    input,
    textarea {
      font-size: 0.813rem;
      font-weight: 600;
    }
  }
  .shipping-method-container,
  .payment-card-container {
    position: relative;
    padding: 0.625rem;
    border: 1px solid $input-border;
    border-radius: $default-radius;
    .form-check-input {
      position: absolute;
      inset-inline-end: 0.75rem;
      inset-block-start: 1.2rem;
    }
    @media (min-width: 576px) {
      .shipping-partner-details,
      .saved-card-details {
        width: 12.5rem;
      }
    }
  }
  }
.checkout-payment-success {
  text-align: center;
  img {
    width: 200px;
    height: 200px;
  }
}
@media screen and (max-width: 575px) {
  #shipped-tab-pane {
    .btn-group {
      .btn {
        width: 100%;
        border-radius: $default-radius;
      }
    }
  }
}
.payment-card{
  position: absolute;
  inset-inline-end: 0;
  width: 100%;
  height: 100%;
  border-radius: 6px;
  border: 1px solid var(--default-border);
  background: transparent;
  inset-block-start: 0rem;
  margin-top: 0px !important;
  z-index: 0;
  border-radius: 3px !important;
  &:checked{
    width: 100%;
    height: 100%;
    left: 0;
    right: 0;
    top: 0px;
    border: 0;
    position: absolute;
    background:$primary-02;
    border: 1px solid $primary !important;
    border-radius: 3px !important;
  }
  &.form-check-input:checked[type=radio]{
     --bs-form-check-bg-image:none !important;
  }
}
.payment-card-style {
  height: 80px;
  border-radius: 5px;
  position: relative;
  z-index: 9;
}
/* End:: Checkout */

/* Start:: Orders */
.orders-delivery-address {
  width: 75%;
}
.delivery-date {
  width: 60px;
  height: 60px;
  padding: 10px;
  background-color: $primary-005;
  align-items: center;
  justify-content: center;
  display: flex;
  flex-direction: column;
  border-radius: 0.3rem;
  animation-fill-mode: backwards;
}
/* End:: Orders */

/* Start:: Order Details */

.order-track {
   .accordion {
     .accordion-item {
      position: relative;

      &::before {
        content: "";
        position: absolute;
        inset-inline-start: 19px;
        inset-block-start: 32px;
        height: 100%;
        border-inline-start: 1px dashed $primary-03;
      }

      &.next-step {
        &::before {
          border-inline-start-color: $default-border;
        }

        .sub-track::before {
          border-color: $default-border;
        }
      }

      .sub-track {
        position: relative;

        &::before {
          content: "";
          position: absolute;
          height: 10px;
          width: 10px;
          background-color: $primary-03;
          border: 3px solid $primary-02;
          border-radius: 50%;
          inset-inline-start: -34px;
          inset-block-start: 50%;
          transform: translateY(-50%);
        }

        &:last-child {
          margin-block-end: 20px;
        }

        + .sub-track {
          margin-block-start: 15px;
        }
      }
    }
  }

  :last-child > .accordion-item {
    &::before {
      content: unset;
    }
  }
}
/* End:: Order Details */

/* Start:: Add Products & Edit Products */
.add-products {
  .tab-style-2 .nav-item .nav-link {
    padding: 0.85rem 2rem;
  }
}
.color-selection {
  .choices__input {
    width: 6.25rem;
  }
}
.product-image-default {
  width: 100%;
  height: 16.25rem;
}
.productupload-image-container,
.productupload-image-container1,
.productupload-image-container2 {
  .filepond--root[data-style-panel-layout~="circle"] {
    border-radius: $default-radius;
    width: 100%;
  }
  .filepond--drop-label.filepond--drop-label label {
    padding: 2em;
  }
  .filepond--drop-label {
    color: $text-muted !important;
  }
  .filepond--panel-root {
    border: 0.125rem dashed $input-border !important;
  }
  .filepond--root[data-style-panel-layout~="circle"]
    .filepond--image-preview-wrapper {
    border-radius: 0.3rem;
  }
}
.productupload-image-container {
  .filepond--root[data-style-panel-layout~="circle"] {
    height: 16.25rem !important;
  }
  .filepond--drop-label label {
    font-size: 0.75rem;
  }
  .filepond--root[data-style-panel-layout~="circle"]
    .filepond--image-preview-wrapper,
  .filepond--file {
    height: 16.25rem;
  }
}
.productupload-image-container1,
.productupload-image-container2 {
  .filepond--root[data-style-panel-layout~="circle"] {
    height: 7.375rem !important;
  }
  .filepond--drop-label label {
    font-size: 0.75rem;
  }
  .filepond--root[data-style-panel-layout~="circle"]
    .filepond--image-preview-wrapper,
  .filepond--file {
    height: 7.375rem !important;
  }
  .filepond--file [data-align*="bottom"] {
    bottom: 9.125em;
  }
}
.product-img-icon {
  font-size: 1.625rem !important;
  opacity: 0.5;
}
.product-documents-container {
  .filepond--root {
    width: 100%;
  }
  .filepond--panel-root {
    border-color: $input-border !important;
    border-radius: $default-radius;
  }
  .filepond--root .filepond--drop-label {
    label {
      color: $text-muted;
    }
  }
}
#product-features {
  max-height: 12.5rem;
  overflow-y: scroll;
}
.product-image{
  position: relative;
  margin-left: auto;
  margin-right: auto;
  border-radius: 0.25rem;
  border-width: 1px;
  --tw-border-opacity: 1;
  border-color: rgb(226 232 240 / var(--tw-border-opacity));
  --tw-bg-opacity: 1;
  background-color: $light;
  text-align: center;
  align-items: center;
  display: flex;
}
.product-badge{
  position: absolute;
}
.wishlist-product-badge{
  position: absolute;
  inset-block-end: 0.5rem;
  inset-inline-start: 0.5rem;
}
.color-btn{
  padding: 0.75rem;
}

@media (min-width: 1600px) {
  .product-details-page .swiper-slide img {
    height: 30rem;
  }
  .product-image img {
    height: 250px;
  }
}
.product-details-page .swiper-slide img {
  padding: 1.25rem;
}
@media (min-width: 480px){
  .product-des{ 
    .choices, .choices__inner {
      width: 13rem !important;
  }
  }
}
.swiper-thumbs .swiper-slide img{
  padding: 10px;
}
.product-svg{
  width: 3rem;
  height: 3rem;
  margin-inline-end: 10px;
}
.order-product{
  width: 6rem !important;
  height: 6rem !important;
}
@media (max-width: 1599px){
  .product-image img {
    width: 100%;
  }
}

#product-category {
  .choices__list--dropdown .choices__item--selectable::after,
  .choices__list[aria-expanded] .choices__item--selectable::after {
    display: none !important;
  }

  .choices__list--dropdown .choices__item--selectable,
  .choices__list[aria-expanded] .choices__item--selectable {
    padding-inline-end: 0 !important;
  }
}

/* End:: Add Products & Edit Products */