/* Basic Alerts */

/* Solid Colored Alerts */
@mixin alert-solid-color($color) {
    background-color: $color;
    color: $white;
    border-color:  $color;
    .btn-close {
        color: $white;
    }
}
.alert-solid-primary {
    @include alert-solid-color($primary);
}
.alert-solid-secondary {
    @include alert-solid-color($secondary);
}
.alert-solid-warning {
    @include alert-solid-color($warning);
}
.alert-solid-info {
    @include alert-solid-color($info);
}
.alert-solid-success {
    @include alert-solid-color($success);
}
.alert-solid-danger {
    @include alert-solid-color($danger);
}
.alert-solid-light {
    background-color: $light;
    color: $default-text-color;
    border-color:  $default-border;
}
.alert-solid-dark {
    @include alert-solid-color($dark);
    color: $custom-white !important;
    .btn-close {
        color: $custom-white !important;
    }
}
/* Solid Colored Alerts */

/* Outline Alerts */
@mixin outline-alert($color) {
    background-color: $custom-white;
    color: $color;
    border-color: $color;
    .btn-close {
        color: $color;
    }
}
.alert-outline-primary {
    @include outline-alert($primary);
}
.alert-outline-secondary {
    @include outline-alert($secondary);
}
.alert-outline-info {
    @include outline-alert($info);
}
.alert-outline-warning {
    @include outline-alert($warning);
}
.alert-outline-success {
    @include outline-alert($success);
}
.alert-outline-danger {
    @include outline-alert($danger);
}
.alert-outline-light {
    background-color: $custom-white;
    color: $default-text-color;
    border-color: $light;
}
.alert-outline-dark {
    @include outline-alert($dark);
}
/* Outline Alerts */

/* Customized Alerts */
@mixin custom-alert($color) {
    border-inline-start: 0.313rem solid $color !important;
    color: $text-muted;
    background-color: $custom-white;
    border: 1px solid $default-border;
    font-size: 0.813rem;
    .btn-close {
        color: $default-text-color;
    }
}
.alert-primary.custom-alert-icon {
    @include custom-alert($primary);
}
.alert-secondary.custom-alert-icon {
    @include custom-alert($secondary);
}
.alert-warning.custom-alert-icon {
    @include custom-alert($warning);
}
.alert-danger.custom-alert-icon {
    @include custom-alert($danger);
}
.alert-success.custom-alert-icon {
    @include custom-alert($success);
}
.alert-info.custom-alert-icon {
    @include custom-alert($info);
}
.alert-light.custom-alert-icon {
    @include custom-alert($light);
}
.alert-dark.custom-alert-icon {
    @include custom-alert($dark);
}
/* Customized Alerts */

/* Customized Alerts1 */
.custom-alert1 {
    margin-block-end: 0;
    background-color: $custom-white;
    border: 0;
    padding: 1.25rem;
    color: $default-text-color;
    p {
        margin-block-end: 2.5rem;
        color: $text-muted;
        font-size: 0.8rem;
    }
    .custom-alert-icon {
        width: 3.125rem;
        height: 3.125rem;
        margin-bottom: 0.85rem;
    }
    .btn-close {
        padding: 0;
        margin-block-end: 1rem;
    }
    &.alert-primary {
        border-block-start: 0.313rem solid $primary;
    }
    &.alert-secondary {
        border-block-start: 0.313rem solid $secondary;
    }
    &.alert-warning {
        border-block-start: 0.313rem solid $warning;
    }
    &.alert-danger {
        border-block-start: 0.313rem solid $danger;
    }
}
/* Customized Alerts1 */

/* Image alerts */
.alert-img {
    display: flex;
    align-items: center;
    .avatar {
        border: 1px solid rgba(0,0,0,0.1);
    }
}
/* Image alerts */

@media screen and (max-width:400px) {
    .btn-close.custom-close {
        margin: 9px 9px 9px 0;
    }  
}
.alert {
  &.style-1{
    padding: 8px;
    .alert_content{
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      max-width: 95%;
    }
  }

  &.style-2{
    .alert_content{
      display: flex;
      align-items: center;
      flex-wrap: wrap;
      max-width: 95%;
      position: relative;

      &::before{
        content: "";
        position: absolute;
        width: 4px;
        height: calc(100%);
        border-radius: 20px;
        inset-inline-start: -9px;
        inset-block-start: 50%;
        transform: translateY(-50%);
        background-color:$light;
      }

      &.warning::before{
        background-color: $warning !important;
      }
      &.primary::before{
        background-color: $primary !important;
      }
      &.secondary::before{
        background-color: $secondary !important;
      }
      &.success::before{
        background-color: $success !important;
      }
      &.info::before{
        background-color: $info !important;
      }
      &.danger::before{
        background-color: $danger !important;
      }
      &.dark::before{
        background-color: $dark !important;
      }
    }
  }

  &.style-3{
    padding: 10px 12px;

    .alert_content{
      display: flex;
      align-items: flex-start;
      max-width: 92%;
    }
  }

  &.style-4{
    border: none;
    padding: 20px 15px;
    border-radius: 23px;

    .alert_content{
      width: 80%;
      margin: 0 auto;
    }
    .alert_inner_icn{
      position: absolute;
      inset-block-start: -13px;
      inset-inline-start: 6px;

      .avatar{
        position: relative;

        &::before{
          content: "";
          height: 12px;
          width: 12px;
          position: absolute;
          inset-block-end: -2px;
          inset-inline-start: 7px;
          border-end-start-radius: 2px;
          border-end-end-radius: 2px;
          transform: rotate(67deg);
          border-start-start-radius: 20px;
          border-start-end-radius: 5px;
        }
        &.bg-success::before{
          background-color:  $success;
        }
        &.bg-warning::before{
          background-color:  $warning;
        }
        &.bg-info::before{
          background-color:  $info;
        }
        &.bg-secondary::before{
          background-color:  $secondary;
        }
        &.bg-danger::before{
          background-color:  $danger;
        }
        &.bg-primary::before{
          background-color:  $primary;
        }
        &.bg-dark::before{
          background-color: $dark;
        }
      }
    }
  }
}